@charset "UTF-8";
/**
 * 尺寸 基础尺寸
 * 命名能在语义的前提下简单就尽量简单, 这里可以是 size-2x, space-2x, size-base ...
 * 不过可以在语义的前提下做的更精简一些, 于是用了s2, s1等
 * 可用变量: `$s1 - $s8`
 * @example scss - 使用
 *   .element {
 *     padding: $s1 !default;
 *   }
 *
 * @example css - CSS 输出
 *   .element {
 *     padding: 4px !default;
 *   }
 */
.next-switch {
  box-sizing: border-box;
  outline: none;
  text-align: left;
  transition: all 0.3s cubic-bezier(0.78, 0.14, 0.15, 0.86);
  overflow: hidden;
  cursor: pointer; }
  .next-switch *,
  .next-switch *:before,
  .next-switch *:after {
    box-sizing: border-box; }
  .next-switch:after {
    content: " ";
    transition: all 0.4s cubic-bezier(0.78, 0.14, 0.15, 0.86);
    transform-origin: left center; }
  .next-switch-medium {
    position: relative;
    display: inline-block;
    border: 1px solid transparent;
    width: 56px;
    height: 26px;
    border-radius: 18px; }
    .next-switch-medium:after {
      border: 1px solid transparent;
      position: absolute;
      left: 100%;
      transform: translateX(-100%);
      width: 24px;
      height: 24px;
      border-radius: 18px;
      box-sizing: border-box; }
    .next-switch-medium > .next-switch-children {
      font-size: 12px;
      position: absolute;
      height: 24px;
      line-height: 24px; }
  .next-switch-small {
    position: relative;
    display: inline-block;
    border: 1px solid transparent;
    width: 44px;
    height: 22px;
    border-radius: 18px; }
    .next-switch-small:after {
      border: 1px solid transparent;
      position: absolute;
      left: 100%;
      transform: translateX(-100%);
      width: 20px;
      height: 20px;
      border-radius: 18px;
      box-sizing: border-box; }
    .next-switch-small > .next-switch-children {
      font-size: 12px;
      position: absolute;
      height: 20px;
      line-height: 20px; }
  .next-switch-on {
    background-color: #BAD9FF; }
    .next-switch-on:after {
      box-shadow: 1px 1px 4px 0px rgba(51, 51, 51, 0.1);
      background-color: #FFFFFF;
      border-color: transparent; }
    .next-switch-on > .next-switch-children {
      left: 10px;
      color: #FFFFFF; }
    .next-switch-on:focus, .next-switch-on:hover {
      background-color: #7DBAFF; }
      .next-switch-on:focus:after, .next-switch-on:hover:after {
        background-color: #FFFFFF; }
  .next-switch-on.next-switch-small > .next-switch-children {
    left: 6px; }
  .next-switch-on[disabled] {
    background-color: #DDF1FF;
    cursor: not-allowed; }
    .next-switch-on[disabled]:after {
      right: 0;
      box-shadow: 1px 1px 4px 0px rgba(51, 51, 51, 0.1);
      background-color: #F5FAFF;
      border-color: #E6E7EB; }
    .next-switch-on[disabled] > .next-switch-children {
      color: #CCCCCC; }
  .next-switch-off {
    background-color: #FFFFFF;
    border-color: #D5E6FC; }
    .next-switch-off:focus, .next-switch-off:hover {
      background-color: #EDF8FF;
      border-color: #D5E6FC; }
    .next-switch-off:after {
      left: 0;
      transform: translateX(0);
      box-shadow: 1px 1px 4px 0px rgba(51, 51, 51, 0.1);
      background-color: #FFFFFF;
      border-color: transparent; }
      .next-switch-off:after:focus, .next-switch-off:after:hover {
        background-color: #FFFFFF; }
    .next-switch-off > .next-switch-children {
      right: 10px;
      color: #999999; }
  .next-switch-off[disabled] {
    background-color: #F5FAFF;
    border: 1px solid transparent;
    cursor: not-allowed; }
    .next-switch-off[disabled]:after {
      box-shadow: 1px 1px 4px 0px rgba(51, 51, 51, 0.1);
      background-color: #F5FAFF;
      border-color: #E6E7EB; }
    .next-switch-off[disabled] > .next-switch-children {
      color: #D5E6FC; }
  .next-switch-off.next-switch-small > .next-switch-children {
    right: 5px; }
